:root{
  --primary-color: #21AD6A;
  --primary-color2: #1f1d2b;
  --secondary: #ADA821;
  --gold: #ada11f;
  --txtWhite: #ffffff;
  --txtBlue: blue;
  --lightGrey: #f0f1f2;
  --placeholderCol: grey;
  --white: white;
  --black: black;
  --txtBlack: black;
  --iconCol: #D1D1D1;
  --iconSize: 26;
  --iconSizeLarge: 35;
  --iconSizeSm: 18;
  --iconSizeExSm: 12;
  --bold: bold;
  --error-color: red;

  --bg-color: #ffffff;
  --bg-color2: #e7f2fd;
  --txt-Black: #000000;
  --txt-Black2: #585858;
  --txt-White: #ffffff;
  --grey: #b6c5d1;

}

button,
input {
  outline: none;
  border: none;
  background-color: transparent;
  font-family: "Quicksand", sans-serif;
}

.container {
  padding-top: 120px;
  padding-left: 120px;
  padding-right: 50px;
  padding-bottom: 50px;

}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

a {
  text-decoration: none;
  color: #000000;
  color: var(--txt-Black);
}

.margin__bottom__50 {
  margin-bottom: 50px;
}

li {
  list-style: none;
}

.signin {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  overflow-y: hidden;
  color: var(--txt-Black);
  background-color: var(--bg-color);
}

.error {
  color: var(--error-color);
  text-align: center;
}

.signin .shape__box,
.signin .form__box {
  flex: 50% 1;
}

.signin .shape__box {
  position: relative;
}

.signin .shape__box .shape__1,
.signin .shape__box .shape__2,
.signin .shape__box .shape__3 {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}

.signin .shape__box .shape__1 {
  /* width: 85%; */
  margin-left: 0;
  z-index: 5;
}

.signin .shape__box .shape__2 {
  /* width: 90%; */
  margin-left: 25px;
  z-index: 4;
}

.signin .shape__box .shape__3 {
  /* width: 95%; */
  margin-left: 50px;
  z-index: 3;
}

.signin .shape__box .shape__4 {
  width: 40%;
  position: absolute;
  top: 50%;
  left: 2%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 6;
}

.signin .shape__box .shape__1 img,
.signin .shape__box .shape__2 img,
.signin .shape__box .shape__3 img,
.signin .shape__box .shape__4 img {
  width: 100%;
  height: 100%;
}

.signin .form__box {
  position: relative;
}

.signin .form__box .logo {
  position: absolute;
  right: 5%;
  top: 5%;
  width: 60px;
  height: 60px;
}

.signin .form__box .logo img {
  width: 100%;
  height: 100%;
}

/* form style */
.signin .form__box .form {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.signin .form__box .form #error {
  color: red;
}

.signin .form__box .form h1 {
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 48px;
}

.signin .form__box .form .email__field,
.signin .form__box .form .password__field {
  margin: 20px 0;
  width: 350px;
}

.signin .form__box .form .email__field input {
  margin: 5px 0;
  background: var(--bg-color);
  border: 1px solid var(--primary-color);
  box-sizing: border-box;
  border-radius: 10px;
  padding: 10px 20px;
  width: 100%;
}

.signin .form__box .form .password__field input {
  margin: 5px 0;
  padding: 10px 20px;
  width: 100%;
  background: var(--bg-color);
  border: 1px solid #f0f3f6;
  box-sizing: border-box;
  box-shadow: 6px 12px 15px rgba(51, 51, 51, 0.04);
  border-radius: 10px;
}

.signin .form__box .form .password__input__area {
  position: relative;
}

.signin .form__box .form .password__input__area .password__icon {
  position: absolute;
  bottom: 12px;
  right: 10px;
  cursor: pointer;
}

.signin .form__box .form #signin__btn {
  /* background: #d59344; */
  background: var(--primary-color);
  border-radius: 5px;
  width: 350px;
  color: var(--txt-White);
  padding: 10px;
  cursor: pointer;
  /* border: 1px solid var(--primary-color); */
}
.shape__1{
color: var(--primary-color);
}

@media screen and (max-width: 999px) {
  .signin .shape__box {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .signin .form__box .form h1,
  .signin .form__box .form p {
    text-align: center;
  }
  .signin .form__box .form h1 {
    font-size: 28px;
  }
  .signin .form__box .form p {
    font-size: 14px;
  }
  .signin .form__box .form form {
    width: 80%;
  }
  .signin .form__box .form .email__field,
  .signin .form__box .form .password__field,
  .signin .form__box .form #signin__btn {
    width: 100%;
  }
}

.sidebar {
  position: fixed;
  width: 70px;
  height: 100%;

  background: var(--primary-color2);
  transition: 0.3s;
  overflow: hidden;
  color: var(--txt-White);
  border-radius: 10px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  z-index: 1000;
}

.sidebar ul {
  position: absolute;
  top: 70px;
  left: 0;
  width: calc(100% - 10px);
  margin: 0 5px;
}

.sidebar ul li {
  position: relative;
  width: 100%;
  list-style: none;
  border-bottom-left-radius: 15px;
  border-top-left-radius: 15px;
}

.sidebar ul li:hover a,
.sidebar ul li a.active {
  background-color: var(--primary-color);
  color: var(--txt-White);
  border-radius: 10px;
}

.sidebar ul li a {
  position: relative;
  display: block;
  width: 100%;
  display: flex;
  text-decoration: none;
  color: var(--primary-color);
  font-size: 16px;
  font-weight: bold;
}

.sidebar ul li a .icon {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 24px;
}

.sidebar ul li a.active .icon svg {
  font-size: 24px;
  fill: white;
}

.topbar {
  font-family: "Quicksand", sans-serif;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
 color: var(--bg-color);
  background-color: var(--primary-color);
  padding-left: 120px;
  padding-right: 50px;
  position: fixed;
  box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.2);
  z-index: 999;
}

.topbar h1 {
  font-family: Quicksand;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
}

.topbar .profile {
  display: flex;
  align-items: center;
  justify-content: center;
}

.topbar .profile p {
  font-size: 14px;
  margin: 0 10px;
  font-weight: bold;
}

.topbar .menu {
  background-color: var(--bg-color);
  position: absolute;
  top: 80px;
  right: 40px;
  padding: 10px 50px;
  border-radius: 10px;
  border: 1px solid var(--bg-color2);
  transition: all 0.5s ease;
}

.topbar .menu p {
  font-size: 16px;
  font-weight: 500;
  margin: 5px 0;
  cursor: pointer;
  color: black;
}

.count__box{
  width: 100%;
  background-color: red;
  background: var(--bg-color);
  border: 0.928364px solid #f0f3f6;
  box-shadow: 0px 4px 200px rgba(0, 0, 0, 0.04);
  border-radius: 10px;
  padding: 30px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
  /* margin-bottom: 30px; */
}

.count__box .vertical__line {
  width: 2px;
  height: 100px;
  background-color: #ececee;
}

.count__box .inner__box {
  display: flex;
  align-items: center;
  max-width: 400px;
}

.count__box .inner__box .icon {
  padding: 20px;
  opacity: 0.65;
  border-radius: 18.5673px;
  margin: 0 20px;
}

.count__box .inner__box .icon__1 {
  background: rgba(242, 201, 76, 0.35);
}

.count__box .inner__box .icon__2 {
  background: rgba(75, 181, 67, 0.35);
}

.count__box .inner__box .icon__3 {
  background: rgba(155, 81, 224, 0.35);
}

.count__box .inner__box .data h2 {
  color: var(--txt-Black2);
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
}

.count__box .inner__box .data h1 {
  color: var(--txt-Black);
  font-weight: 500;
  font-size: 21px;
  line-height: 28px;
}

@media screen and (max-width: 1024px) {
  .count__box {
    flex-direction: column;
  }

  .count__box .inner__box {
    width: 100%;
    margin: 30px 0;
    align-items: center;
    justify-content: center;
  }

  .count__box .vertical__line {
    width: 100%;
    height: 2px;
  }

  .count__box .inner__box .icon__2 {
    margin-left: -20px;
  }
}

.bottom__stats {
  display: flex;
  align-items: center;
  justify-content: center;
}

.bottom__stats .left__side,
.bottom__stats .right__side {
  width: 50%;
}

.bottom__stats .right__side {
  padding: 20px;
}

.bottom__stats .right__side .inner__box {
  margin: 20px;
  background: var(--bg-color);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border: 0.928364px solid #f0f3f6;
  box-shadow: 0px 4px 200px rgba(0, 0, 0, 0.04);
  padding: 30px 0;
}

.bottom__stats .right__side .inner__box .icon {
  padding: 20px;
  opacity: 0.65;
  border-radius: 100%;
  margin: 0 20px;
}

.bottom__stats .right__side .inner__box .icon__4 {
  background: #e8d7f8;
}

.bottom__stats .right__side .inner__box .icon__5 {
  background: #cfba6b;

}
.bottom__stats .right__side .inner__box .icon__5 img{
  width: 70px;
  height: 70px;
  color: cadetblue;

}

.bottom__stats .right__side .inner__box .data h2 {
  color: var(--txt-Black2);
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
}

.bottom__stats .right__side .inner__box .data h1 {
  color: var(--txt-Black);
  font-weight: 500;
  font-size: 21px;
  line-height: 28px;
}

@media screen and (max-width: 1024px) {
  .bottom__stats {
    flex-direction: column;
  }
  .bottom__stats .left__side,
  .bottom__stats .right__side {
    width: 100%;
  }
}

.table__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
}

.table__top input {
  background: var(--bg-color2);
  border-radius: 5px;
  padding: 10px 30px;
}

.table__top button {
  background: var(--primary-color);
  color: var(--txt-White);
  border-radius: 5px;
  padding: 10px 15px;
  margin: 0 5px;
  cursor: pointer;
}

.table__top .search {
  position: relative;
}

.table__top .search svg {
  position: absolute;
  top: 10px;
  left: 10px;
}

.table__container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
  color: var(--txt-Black);
  position: relative;
}

.table__container .menu__dots {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.table__container .menu__dots div {
  width: 5px;
  height: 5px;
  background-color: var(--txt-Black);
  margin: 0 3px;
  border-radius: 50%;
}

.table__container .menu {
  /* width: 100px; */
  background-color: var(--bg-color);
  position: absolute;
  right: 140px;
  padding: 10px 50px;
  border-radius: 10px;
  border: 1px solid var(--bg-color2);
}

.table__container .menu p {
  font-size: 16px;
  font-weight: 500;
  margin: 5px 0;
}

.table__container .menu .horizontal__line {
  width: calc(100% + 100px);
  height: 1px;
  margin-left: -50px;
  background-color: var(--bg-color2);
}

.table__container h2 {
  font-size: 26px;
  margin-bottom: 20px;
  text-align: center;
}

.responsive-table li {
  border-radius: 3px;
  padding: 25px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}

.responsive-table .table-header {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  color: var(--txt-Black);
  font-weight: bold;
}
.responsive-table .table-row {
  background: var(--bg-color);
  border: 1px solid var(--bg-color2);
  box-sizing: border-box;
  border-radius: 10px;
}
.responsive-table .table-row p.status {
  padding: 10px;
  border-radius: 10px;
}
.responsive-table .table-row p.success {
  background-color: rgba(51, 137, 44, 0.15);
  color: #33892c;
}
.responsive-table .table-row p.primary {
  background-color: #f4f6f8;
  color: #b6c5d1;
}
.responsive-table .table-row p.warning {
  background-color: #fdf7e5;
  color: #d29e00;
}
.responsive-table .table-row img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  margin-right: 10px;
}
.responsive-table .col-1 {
  flex-basis: 10%;
}
.responsive-table .col-2 {
  flex-basis: 40%;
}
.responsive-table .col-3 {
  flex-basis: 25%;
}
.responsive-table .col-4 {
  flex-basis: 25%;
}
@media all and (max-width: 767px) {
  .table__container .menu {
    /* top: 0px; */
    /* right: 0; */
    right: 180px;
    margin-top: 35px;
  }
  .responsive-table .table-header {
    display: none;
  }
  .responsive-table li {
    display: block;
  }
  .responsive-table .col {
